.form-container {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 10px;
align-items: center;
padding: 10px;
color:red;
justify-content: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.3);
  
}
.form-container::before,.form-container::after{
    
    opacity: 1;
}

.headwe{
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    justify-items: center;
    align-items: center;
}
.headwe>button{
    width:25%;
    margin: 0 auto;
}

.s-container{
    width: 80%;
    margin: 0px auto;
    justify-self: center;
    align-self: center;
    justify-content: center;
    margin-bottom: 10px ;
object-fit: contain;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
    background: linear-gradient(135deg,#a030308e 10%, rgb(255, 255, 255) 100%);
    

  }
  .s-container::before,.s-container::after{
      opacity: 0.3;
  }
  