:root{
    --primary: #C6373A;
}

.form-edit{
    display: inline-block;
    width: calc(50%-1px);
}
.btn {
    padding: 8px 20px;
    border-radius: 10px;
    outline:none;
    border: none;
    cursor: pointer;
}
.btn--norad {
    padding: 6px 20px;
    outline:none;
    border: none;
    cursor: pointer;
    
}

.btn--primary{
    background-color: var(--primary);
    color: #fff;
    border: 1px solid var(--primary);
}
.btn--primary:hover{
    background-color: #fff;
    color: #C6373A;
}

.btn--outline{
    background-color: transparent;
    color: #C6373A;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-in;
}
.btn--none{
    background-color: transparent;
    color: #C6373A;
    padding: 8px 20px;
    transition: all 0.3s ease-in;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}
